import React, { forwardRef, useImperativeHandle, useMemo, useRef } from 'react';
import { Button, Text } from 'tradewize-component';
import { ScrollView, StyleSheet, View } from 'react-native';
import BottomSheet, { BottomSheetHandleProps } from '@gorhom/bottom-sheet';
import { colors } from '../../../theme';
import { IconClose } from '../../../assets/svg';
import { SvgXml } from 'react-native-svg';

export interface BottomSheetRef {
  open: () => void;
  close: () => void;
}

export interface BottomSheetProps {
  onClose?: () => void;
}

const CustomHandle: React.FC<BottomSheetHandleProps> = props => {
  return (
    <View style={styles.indicatorContainer}>
      <Text style={styles.indicatorText}>About Your Score</Text>
      <Button style={styles.closeButton}>
        <SvgXml xml={IconClose(colors.white)} width={24} height={24} />
      </Button>
    </View>
  );
};

const AboutYourScore = forwardRef<BottomSheetRef, BottomSheetProps>(
  (props, ref) => {
    const snapPoints = useMemo(() => ['90%'], []);
    const bottomSheetRef = useRef<BottomSheet>(null);

    useImperativeHandle(ref, () => ({
      open: () => {
        bottomSheetRef?.current?.snapToIndex(0);
      },
      close: () => {
        bottomSheetRef?.current?.close();
      },
    }));

    return (
      <BottomSheet
        ref={bottomSheetRef}
        index={-1}
        animateOnMount={true}
        enableContentPanningGesture={true}
        enableHandlePanningGesture={true}
        enablePanDownToClose={false}
        enableDynamicSizing={false}
        containerStyle={styles.containerPosition}
        keyboardBehavior="interactive"
        keyboardBlurBehavior="restore"
        snapPoints={snapPoints}
        style={styles.container}
        handleComponent={CustomHandle}
        enableOverDrag={false}
      >
        <View style={styles.contentContainer}>
          {/* <ScrollView
            contentContainerStyle={styles.contentContainer}>
            <Text>About Your Score</Text>
          </ScrollView> */}
        </View>
      </BottomSheet>
    );
  },
);

const styles = StyleSheet.create({
  containerPosition: {
    flex: 1,
  },
  container: {
    flex: 1,
  },
  contentContainer: {
    flex: 1,
  },
  indicatorContainer: {
    backgroundColor: colors.mainColorPrimary,
    paddingHorizontal: 16,
    borderTopLeftRadius: 12,
    borderTopRightRadius: 12,
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  indicatorText: {
    color: colors.white,
    fontSize: 16,
    fontWeight: 'bold',
    lineHeight: 24,
  },
  closeButton: {
    paddingVertical: 16,
    paddingHorizontal: 16,
    minHeight: 0,
    backgroundColor: colors.mainColorPrimary,
  },
});

export default AboutYourScore;
