import React, { useCallback } from 'react';
import {
  View,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Keyboard,
  FlatList,
  ActivityIndicator,
  TextInput,
} from 'react-native';
import { Text, Button, SCREEN_WIDTH } from 'tradewize-component';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { SvgXml } from 'react-native-svg';
import BottomSheet, {
  BottomSheetScrollView,
  useBottomSheetSpringConfigs,
} from '@gorhom/bottom-sheet';
import FastImage from 'react-native-fast-image';

import { colors } from '../../theme';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import {
  IconClose,
  IconPlus,
  IconCamera,
  IconVideo,
} from '../../assets/svg';
import { CommentChecklistProps, MediaFile } from './types';
import { useCommentChecklist } from './useCommentChecklist';
import { styles } from './styles';
import { useAppSelector } from '../../../../Auth/src/redux/store/types';
import { dispatch } from '../../helper';
import { hideCommentChecklistModal } from '../../../../Auth/src/redux/features/app/actions';

// TODO: Import actual CameraModal and VideoModal components
// import { CameraModal, VideoModal } from 'tradewize';

const CommentChecklist = () => {
  const { t } = useAppTranslation();
  const insets = useSafeAreaInsets();
  const {
    bottomSheetRef,
    localItem,
    isLoadingPhoto,
    isLoadingVideo,
    isLastCommentFromOthers,
    snapPoints,
    shouldUseDynamicSizing,
    handleSheetChanges,
    showMediaOptions,
    handleRemoveMedia,
    handleCommentChange,
    handlePreviewMedia,
    handleSave,
    getMediaList,
    readonly,
    isLoadingUpload,
    item,
    visible,
  } = useCommentChecklist();

  const animationConfigs = useBottomSheetSpringConfigs({
    damping: 40,
    overshootClamping: true,
    stiffness: 800,
  });

  const renderMediaItem = useCallback(
    ({ item: mediaItem }: { item: MediaFile }) => (
      <Button
        onPress={() => handlePreviewMedia(mediaItem)}
        style={styles.imageContainer}
      >
        {!readonly && (
          <Button
            style={styles.removeButton}
            onPress={() => handleRemoveMedia(mediaItem)}
          >
            <SvgXml xml={IconClose(colors.black)} width={16} height={16} />
          </Button>
        )}
        <FastImage
          source={{ uri: mediaItem?.thumbnailPreview || mediaItem?.uri }}
          style={styles.imageThumbnail}
          resizeMode="cover"
        />
        {mediaItem?.type?.includes('video') && (
          <View style={styles.videoIcon}>
            <SvgXml xml={IconVideo(colors.white)} width={24} height={24} />
          </View>
        )}
      </Button>
    ),
    [readonly, handlePreviewMedia, handleRemoveMedia],
  );

  const renderMediaGrid = useCallback(
    (type: 'photo' | 'video') => {
      const mediaList = getMediaList(type);
      if (!mediaList?.length && readonly) {
        return null;
      }
      const isLoading = type === 'photo' ? isLoadingPhoto : isLoadingVideo;

      return (
        <View style={styles.mediaGrid}>
          <FlatList
            horizontal
            contentContainerStyle={{
              gap: 12,
              flexWrap: 'wrap',
              width: SCREEN_WIDTH - 32,
            }}
            data={mediaList}
            keyExtractor={(mediaItem, index) => mediaItem?.uri || `${index}`}
            getItemLayout={(_data, index) => ({
              length: 60,
              offset: 72 * index,
              index,
            })}
            removeClippedSubviews={true}
            maxToRenderPerBatch={5}
            windowSize={10}
            initialNumToRender={3}
            renderItem={renderMediaItem}
            scrollEnabled={false}
            showsHorizontalScrollIndicator={false}
            ListFooterComponent={() =>
              !readonly ? (
                <Button
                  style={styles.addButton}
                  onPress={() => showMediaOptions(type)}
                  disabled={isLoading}
                >
                  {isLoading ? (
                    <View style={styles.uploadProgress}>
                      <ActivityIndicator
                        size="small"
                        color={colors.mainColorPrimary}
                      />
                    </View>
                  ) : (
                    <SvgXml
                      xml={IconPlus(colors.grayScale70)}
                      width={24}
                      height={24}
                    />
                  )}
                </Button>
              ) : null
            }
          />
        </View>
      );
    },
    [
      getMediaList,
      readonly,
      isLoadingPhoto,
      isLoadingVideo,
      showMediaOptions,
      renderMediaItem,
    ],
  );

  const renderFileRequirements = useCallback(() => {
    const hasResponses =
      (item?.response?.length ?? 0) > 0 || (item?.responses?.length ?? 0) > 0;

    // Case 2: Has responses but last comment is from others
    //  Require user to add at least 1 photo or video
    if (hasResponses && isLastCommentFromOthers) {
      return (
        <View style={styles.fileRequirements}>
          <Text style={styles.requirementTitle}>{t('FILE_REQUIREMENTS')}:</Text>
          <Text style={styles.requirementText}>
            <SvgXml
              xml={IconCamera(colors.grayScale70)}
              width={14}
              height={14}
            />{' '}
            {t('ADD_AT_LEAST_ONE_PHOTO_OR_VIDEO')}
          </Text>
        </View>
      );
    }

    // Case 1: No responses yet  show fileSettings requirements
    if (
      hasResponses ||
      !item?.fileSettings ||
      !Array.isArray(item.fileSettings) ||
      item.fileSettings.length === 0
    ) {
      return null;
    }

    return (
      <View style={styles.fileRequirements}>
        <Text style={styles.requirementTitle}>{t('FILE_REQUIREMENTS')}:</Text>
        {item?.fileSettings?.map((setting: any, index: number) => (
          <Text key={index} style={styles.requirementText}>
            <SvgXml
              xml={
                setting.fileType === 0
                  ? IconCamera(colors.grayScale70)
                  : IconVideo(colors.grayScale70)
              }
              width={14}
              height={14}
            />{' '}
            {setting.minFiles} ~ {setting.maxFiles}{' '}
            {setting.fileType === 0 ? t('PHOTO') : t('VIDEO')}s {t('REQUIRED')}
          </Text>
        ))}
      </View>
    );
  }, [
    item?.fileSettings,
    item?.response,
    item?.responses,
    isLastCommentFromOthers,
    t,
  ]);

  return (
    <BottomSheet
      ref={bottomSheetRef}
      index={visible ? 0 : -1}
      snapPoints={snapPoints}
      onChange={handleSheetChanges}
      animationConfigs={animationConfigs}
      enablePanDownToClose={false}
      backgroundStyle={styles.bottomSheetBackground}
      handleIndicatorStyle={styles.bottomSheetIndicator}
      enableDynamicSizing={shouldUseDynamicSizing}
      enableOverDrag={true}
      enableContentPanningGesture={true}
      keyboardBehavior="interactive"
      keyboardBlurBehavior="restore"
      android_keyboardInputMode="adjustResize"
    >
      <View style={styles.content}>
        <BottomSheetScrollView
          style={styles.scrollView}
          contentContainerStyle={[
            styles.scrollViewContent,
            shouldUseDynamicSizing && { flexGrow: 0 },
          ]}
          showsVerticalScrollIndicator={true}
          bounces={true}
          nestedScrollEnabled={true}
          scrollEnabled={true}
          overScrollMode="always"
          automaticallyAdjustKeyboardInsets={true}
          keyboardShouldPersistTaps="handled"
          keyboardDismissMode="on-drag"
        >
          <TouchableWithoutFeedback onPress={Keyboard.dismiss}>
            <View>
              {/* Header Section */}
              <View style={styles.infoSection}>
                <View style={styles.itemNameRow}>
                  <Text style={styles.itemName}>{item?.name}</Text>
                  <Button
                    variant="secondary"
                    style={styles.itemCloseButton}
                    onPress={() => dispatch(hideCommentChecklistModal())}
                  >
                    <SvgXml
                      xml={IconClose(colors.black)}
                      width={20}
                      height={20}
                    />
                  </Button>
                </View>
                {renderFileRequirements()}
              </View>

              {/* Comment Input */}
              <View>
                <Text style={styles.labelComment}>{t('COMMENT')}</Text>
                <View style={styles.commentInputContainer}>
                  <TextInput
                    placeholder={t('ADD_COMMENT')}
                    placeholderTextColor={colors.grayScale50}
                    multiline={true}
                    style={styles.commentInput}
                    value={(localItem as any)?.comment || ''}
                    onChangeText={handleCommentChange}
                    editable={!readonly}
                  />
                </View>
              </View>

              {/* Photos Section */}
              <View style={styles.mediaSection}>
                <Text style={styles.sectionTitle}>{t('PHOTOS')}</Text>
                {renderMediaGrid('photo')}
              </View>

              {/* Videos Section */}
              <View style={styles.mediaSection}>
                <View style={{ flexDirection: 'row', alignItems: 'baseline' }}>
                  <Text style={styles.sectionTitle}>{t('VIDEOS')}</Text>
                  <Text style={styles.videoTimeHint}>
                    {t('MIN_MAX_VIDEO_TIME', {
                      min: 3,
                      max: 60,
                    })}
                  </Text>
                </View>
                {renderMediaGrid('video')}
              </View>
            </View>
          </TouchableWithoutFeedback>
        </BottomSheetScrollView>

        {/* Save Button */}
        {!readonly && (
          <View style={{ paddingBottom: insets.bottom }}>
            <Button
              disabled={isLoadingUpload || isLoadingPhoto || isLoadingVideo}
              style={styles.saveButton}
              onPress={handleSave}
            >
              <Text style={styles.saveButtonText}>{t('SUBMIT')}</Text>
            </Button>
          </View>
        )}

        {/* Loading Overlay */}
        {isLoadingUpload && (
          <View style={styles.loadingOverlay}>
            <ActivityIndicator size="large" color={colors.mainColorPrimary} />
          </View>
        )}
      </View>
    </BottomSheet>
  );
};

export default React.memo(CommentChecklist);
