import React, { useState } from 'react';
import { View, TextInput, Alert } from 'react-native';
import { SvgXml } from 'react-native-svg';
import ImagePicker from 'react-native-image-crop-picker';
import {
  IconGallery,
  IconCamera,
  IconSend,
  IconClose,
  IconPlay,
} from '../../../../assets/svg';
import { colors } from '../../../../theme';
import { styles } from './styles';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { Button } from 'tradewize-component';
import FastImage from 'react-native-fast-image';
import Video from 'react-native-video';
import { SelectedMedia } from '../../../../types/FeedBack';

interface CommentInputProps {
  onSendComment: (
    comment: string,
    images?: {
      uri: string;
      path: string;
      mime: string;
      type?: 'image' | 'video';
    }[],
  ) => void;
  onFocus?: () => void;
}

const MAX_MEDIA = 5;

export default function CommentInput({
  onSendComment,
  onFocus,
}: CommentInputProps) {
  const { t } = useAppTranslation();
  const [comment, setComment] = useState('');
  const [selectedMedia, setSelectedMedia] = useState<SelectedMedia[]>([]);

  const handleSend = () => {
    if (comment?.trim() || selectedMedia?.length > 0) {
      onSendComment(comment, selectedMedia);
      setComment('');
      setSelectedMedia([]);
    }
  };

  const handleFocus = () => {
    onFocus?.();
  };

  const handleSelectFromGallery = async () => {
    if (selectedMedia?.length >= MAX_MEDIA) {
      Alert.alert(
        t('WARNING'),
        t('MAXIMUM_IMAGES_REACHED', { max: MAX_MEDIA }),
      );
      return;
    }

    try {
      const remainingSlots = MAX_MEDIA - (selectedMedia?.length || 0);

      const media = await ImagePicker.openPicker({
        multiple: true,
        maxFiles: remainingSlots,
        mediaType: 'any', // Support both images and videos
        cropping: false,
      });

      const newMedia = (Array.isArray(media) ? media : [media]).map(
        (item: any): SelectedMedia => ({
          uri: item?.path,
          path: item?.path,
          mime: item?.mime,
          type: item?.mime?.startsWith('video/') ? 'video' : 'image',
        }),
      );

      // Only take up to remaining slots to ensure we don't exceed MAX_MEDIA
      const mediaToAdd = newMedia?.slice(0, remainingSlots);

      if (newMedia?.length > remainingSlots) {
        Alert.alert(
          t('WARNING'),
          `${t('MAXIMUM_IMAGES_REACHED', { max: MAX_MEDIA })}\nAdded ${
            mediaToAdd?.length
          } of ${newMedia?.length} selected items.`,
        );
      }

      setSelectedMedia(prev => [...(prev || []), ...(mediaToAdd || [])]);
    } catch (error) {
      console.log('User cancelled media picker');
    }
  };

  const handleTakePhoto = async () => {
    if (selectedMedia?.length >= MAX_MEDIA) {
      Alert.alert(
        t('WARNING'),
        t('MAXIMUM_IMAGES_REACHED', { max: MAX_MEDIA }),
      );
      return;
    }

    try {
      const image = await ImagePicker.openCamera({
        mediaType: 'photo',
        cropping: false,
      });

      const newMedia = {
        uri: image?.path,
        path: image?.path,
        mime: image?.mime,
        type: 'image' as const,
      };

      setSelectedMedia(prev => [...(prev || []), newMedia]);
    } catch (error) {
      console.log('User cancelled camera');
    }
  };

  const handleRemoveMedia = (index: number) => {
    setSelectedMedia(prev => prev?.filter((_, i) => i !== index) || []);
  };

  const canSend = comment?.trim() || (selectedMedia?.length || 0) > 0;

  return (
    <View style={styles.container}>
      {/* Media Preview (Images & Videos) */}
      {(selectedMedia?.length || 0) > 0 && (
        <View style={styles.imagesPreviewContainer}>
          {selectedMedia?.map((media, index) => (
            <View key={index} style={styles.imagePreviewItem}>
              {media?.type === 'video' ? (
                // Video thumbnail using react-native-video
                <View style={styles.videoWrapper}>
                  <Video
                    source={{ uri: media?.uri }}
                    style={styles.imagePreview}
                    resizeMode="cover"
                    paused={true}
                    muted={true}
                  />
                  <View style={styles.videoIndicator}>
                    <SvgXml
                      xml={IconPlay(colors.white)}
                      width={20}
                      height={20}
                    />
                  </View>
                </View>
              ) : (
                <FastImage
                  source={{ uri: media?.uri }}
                  style={styles.imagePreview}
                  resizeMode={FastImage.resizeMode.cover}
                />
              )}
              <Button
                style={styles.removeImageButton}
                onPress={() => handleRemoveMedia(index)}
                activeOpacity={0.7}
              >
                <SvgXml xml={IconClose(colors.white)} width={16} height={16} />
              </Button>
            </View>
          ))}
        </View>
      )}

      {/* Input Container */}
      <View style={styles.inputContainer}>
        <Button
          style={styles.iconButton}
          onPress={handleSelectFromGallery}
          activeOpacity={0.7}
        >
          <SvgXml
            xml={IconGallery(colors.grayScale100)}
            width={24}
            height={24}
          />
        </Button>

        <Button
          style={styles.iconButton}
          onPress={handleTakePhoto}
          activeOpacity={0.7}
        >
          <SvgXml
            xml={IconCamera(colors.grayScale100)}
            width={24}
            height={24}
          />
        </Button>

        <TextInput
          style={styles.input}
          placeholder={t('ENTER_YOUR_COMMENT_HERE')}
          placeholderTextColor={colors.grayScale70}
          value={comment}
          onChangeText={setComment}
          onFocus={handleFocus}
          multiline
        />

        <Button
          style={[styles.iconButton, !canSend && styles.sendButtonDisabled]}
          onPress={handleSend}
          disabled={!canSend}
          activeOpacity={0.7}
        >
          <SvgXml
            xml={IconSend(
              canSend ? colors.mainColorPrimary : colors.grayScale70,
            )}
            width={24}
            height={24}
          />
        </Button>
      </View>
    </View>
  );
}
