import React, {
  useState,
  useRef,
  forwardRef,
  useImperativeHandle,
} from 'react';
import { View, TouchableOpacity, ScrollView } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import {
  IconChevronUp,
  IconChevronDown,
  IconPlay,
} from '../../../../assets/svg';
import { colors } from '../../../../theme';
import { styles } from './styles';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { useSelector } from 'react-redux';
import { RootState } from '../../../../../../Auth/src/redux/store/types';
import { logger } from '../../../../utils';
import FastImage from 'react-native-fast-image';
import Video from 'react-native-video';
import { FeedbackResponse } from '../../../../types/FeedBack';
import { isVideo } from '../../../../helper/ImageVideo.helper';
import { formatTimeToMMDDYYYYHHMM } from '../../../../helper/FormatDate.helper';

interface RespondSectionProps {
  feedbackResponses: any[] | null;
}

export interface RespondSectionRef {
  scrollToEnd: () => void;
}

const RespondSection = forwardRef<RespondSectionRef, RespondSectionProps>(
  ({ feedbackResponses }, ref) => {
    const { t } = useAppTranslation();
    const [isExpanded, setIsExpanded] = useState(true);
    const scrollViewRef = useRef<ScrollView>(null);
    const userProfile = useSelector(
      (state: RootState) => state?.auth?.userProfile,
    );

    // Expose scrollToEnd method to parent
    useImperativeHandle(ref, () => ({
      scrollToEnd: () => {
        setTimeout(() => {
          scrollViewRef.current?.scrollToEnd({ animated: true });
        }, 300);
      },
    }));

    const renderMessage = ({ item }: { item: FeedbackResponse }) => {
      if (!item) return null;

      const isMyMessage = item?.accountId === userProfile?.accountId;

      return (
        <View
          style={[
            styles.messageContainer,
            isMyMessage
              ? styles.myMessageContainer
              : styles.otherMessageContainer,
          ]}
        >
          {/* Attachments */}
          {item?.attachments &&
            Array.isArray(item.attachments) &&
            item.attachments.length > 0 && (
              <View style={styles.attachmentsContainer}>
                {item.attachments.map((attachment, index) => {
                  if (!attachment) return null;

                  const isVideoFile = isVideo(attachment?.fileExtension);

                  if (isVideoFile) {
                    return (
                      <TouchableOpacity
                        key={attachment?.attachmentId || index}
                        style={styles.attachmentItem}
                        onPress={() => {
                          // TODO: Open video modal
                          logger('Open video:', attachment?.url);
                        }}
                      >
                        <View style={styles.videoWrapper}>
                          <Video
                            source={{ uri: attachment?.url || '' }}
                            style={styles.attachmentImage}
                            resizeMode="cover"
                            paused={true}
                            muted={true}
                          />
                          <View style={styles.videoIndicator}>
                            <SvgXml
                              xml={IconPlay(colors.white)}
                              width={12}
                              height={12}
                            />
                          </View>
                        </View>
                      </TouchableOpacity>
                    );
                  } else {
                    return (
                      <Button
                        key={attachment?.attachmentId || index}
                        style={styles.attachmentItem}
                        onPress={() => {
                          // TODO: Open image modal
                          logger('Open image:', attachment?.url);
                        }}
                      >
                        <FastImage
                          source={{ uri: attachment?.url || '' }}
                          style={styles.attachmentImage}
                          resizeMode={FastImage.resizeMode.cover}
                        />
                      </Button>
                    );
                  }
                })}
              </View>
            )}

          {/* Comment */}
          {item?.comment &&
            typeof item.comment === 'string' &&
            item.comment.trim() && (
              <View
                style={[
                  styles.messageBubble,
                  isMyMessage
                    ? styles.myMessageBubble
                    : styles.otherMessageBubble,
                ]}
              >
                <Text
                  style={[
                    styles.messageText,
                    isMyMessage
                      ? styles.myMessageText
                      : styles.otherMessageText,
                  ]}
                >
                  {item.comment}
                </Text>
              </View>
            )}

          {/* Name and Time */}
          <View
            style={[styles.messageInfo, isMyMessage && styles.myMessageInfo]}
          >
            <Text style={styles.messageName}>{item?.name || ''}</Text>
            <Text style={styles.messageTime}>
              {item?.time ? formatTimeToMMDDYYYYHHMM(item.time) : ''}
            </Text>
          </View>
        </View>
      );
    };

    return (
      <View style={styles.container}>
        {/* Header */}
        <TouchableOpacity
          style={styles.header}
          onPress={() => setIsExpanded(!isExpanded)}
          activeOpacity={0.7}
        >
          <Text style={styles.headerTitle}>{t('RESPONSES')}</Text>
          <SvgXml
            xml={
              isExpanded
                ? IconChevronUp(colors.grayScale100)
                : IconChevronDown(colors.grayScale100)
            }
            width={24}
            height={24}
          />
        </TouchableOpacity>

        {/* Content */}
        {isExpanded && (
          <View style={styles.content}>
            <View style={styles.divider} />

            {/* Responses List */}
            {feedbackResponses &&
            Array.isArray(feedbackResponses) &&
            feedbackResponses.length > 0 ? (
              <ScrollView
                ref={scrollViewRef}
                style={styles.responsesList}
                showsVerticalScrollIndicator={true}
                nestedScrollEnabled={true}
              >
                {feedbackResponses.map(item => {
                  if (!item || !item.responseId) return null;
                  return (
                    <View key={item.responseId}>{renderMessage({ item })}</View>
                  );
                })}
              </ScrollView>
            ) : (
              <View style={styles.emptyState}>
                <Text style={styles.emptyText}>{t('NO_RESPONSES_YET')}</Text>
              </View>
            )}
          </View>
        )}
      </View>
    );
  },
);

RespondSection.displayName = 'RespondSection';

export default RespondSection;
