import React, { useMemo } from 'react';
import { View, SectionList } from 'react-native';
import { Text, Button } from 'tradewize-component';
import CardItem from '../CardItem';
import DateHeader from '../DateHeader';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { FeedbackItem } from '../../../../types/FeedBack';
import { styles } from '../../styles';

interface ScreenTabProps {
  data: FeedbackItem[];
}

interface SectionData {
  title: string;
  date: Date;
  data: FeedbackItem[];
}

export default function ScreenTab({ data }: ScreenTabProps) {
  const { t } = useAppTranslation();
  // Group data by month/year based on createdAt
  const sections = useMemo(() => {
    // Data is already filtered by tabType in parent component
    const grouped = data.reduce((acc, item) => {
      const dateString = item.createdAt;
      if (!dateString) {
        // If no date, put in "Unknown" group
        const key = 'Unknown';
        if (!acc[key]) {
          acc[key] = { title: key, date: new Date(), data: [] };
        }
        acc[key].data.push(item);
        return acc;
      }

      try {
        const date = new Date(dateString);
        if (isNaN(date.getTime())) {
          const key = 'Unknown';
          if (!acc[key]) {
            acc[key] = { title: key, date: new Date(), data: [] };
          }
          acc[key].data.push(item);
          return acc;
        }

        const month = date.getMonth();
        const year = date.getFullYear();
        const key = `${year}-${month}`;

        if (!acc[key]) {
          acc[key] = { title: key, date, data: [] };
        }
        acc[key].data.push(item);
      } catch {
        const key = 'Unknown';
        if (!acc[key]) {
          acc[key] = { title: key, date: new Date(), data: [] };
        }
        acc[key].data.push(item);
      }

      return acc;
    }, {} as Record<string, SectionData>);

    // Convert to array and sort by date (newest first)
    const sectionsArray = (Object.values(grouped) as SectionData[]).sort(
      (a, b) => {
        if (a.title === 'Unknown') return 1;
        if (b.title === 'Unknown') return -1;
        return b.date.getTime() - a.date.getTime();
      },
    );

    return sectionsArray;
  }, [data]);

  const renderItem = ({ item }: { item: FeedbackItem }) => {
    return <CardItem item={item} />;
  };

  const renderSectionHeader = ({ section }: { section: SectionData }) => {
    if (section.title === 'Unknown') {
      return null;
    }
    return <DateHeader date={section.date} />;
  };

  return (
    <View style={styles.container}>
      <SectionList
        sections={sections}
        renderItem={renderItem}
        renderSectionHeader={renderSectionHeader}
        keyExtractor={item => item.id}
        contentContainerStyle={styles.contentContainer}
        ListEmptyComponent={
          <View style={styles.emptyContainer}>
            <Text>{t('NO_FEEDBACK_FOUND')}</Text>
          </View>
        }
      />
    </View>
  );
}

export const renderTabBar = (props: any) => {
  const { navigationState, jumpTo } = props;

  return (
    <View style={styles.tabBarContainer}>
      {navigationState.routes.map((route: any, index: number) => {
        const isActive = index === navigationState.index;
        return (
          <Button
            key={route.key}
            variant="ghost"
            onPress={() => jumpTo(route.key)}
            style={[
              styles.tabButton,
              isActive && styles.tabButtonActive,
              route.key === 'pending'
                ? styles.tabButtonPending
                : styles.tabButtonCompleted,
            ]}
          >
            <Text style={[styles.tabText, isActive && styles.tabTextActive]}>
              {route?.title}
            </Text>
          </Button>
        );
      })}
    </View>
  );
};
