import { formatRelativeTime } from '../utils';

// Format date to MM/DD/YYYY
export const formatDateToMMDDYYYY = (dateString: string | null): string => {
  if (!dateString) return '';
  try {
    const date = new Date(dateString);
    if (isNaN(date.getTime())) return '';
    return date.toLocaleDateString('en-US', {
      month: '2-digit',
      day: '2-digit',
      year: 'numeric',
    });
  } catch (error) {
    console.error('formatDateToMMDDYYYY err', error);
    return '';
  }
};

// format time to MM/DD/YYYY HH:MM
export const formatTimeToMMDDYYYYHHMM = (timeString: string) => {
  try {
    const date = new Date(timeString);
    const now = new Date();
    const diffInHours = (now.getTime() - date.getTime()) / (1000 * 60 * 60);

    // If message is older than 24 hours, show specific date/time
    if (diffInHours > 24) {
      const day = date.getDate().toString().padStart(2, '0');
      const month = (date.getMonth() + 1).toString().padStart(2, '0');
      const year = date.getFullYear();
      const hours = date.getHours().toString().padStart(2, '0');
      const minutes = date.getMinutes().toString().padStart(2, '0');
      return `${hours}:${minutes}, ${day}/${month}/${year}`;
    }

    // For recent messages, show relative time
    return formatRelativeTime(date.toISOString() || '');
  } catch {
    return timeString;
  }
};
