import React from 'react';
import { View } from 'react-native';
import { Text, Button } from 'tradewize-component';
import { styles } from './styles';
import DateCard from './components/DateCard';
import Header from './components/Header';
import { FeedbackItem } from '../../../../types/FeedBack';
import { dispatch } from '../../../../helper';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { navigationRef } from '../../../../services/NavigationService';
import { SCREEN_ENUM } from '../../../../types/global';
import { logger } from '../../../../utils';
import { formatDateToMMDDYYYY } from '../../../../helper/FormatDate.helper';

interface CardItemProps {
  item: FeedbackItem;
}

export default function CardItem({ item }: CardItemProps) {
  const { t } = useAppTranslation();

  const handleGetFeedbackDetail = async (feedbackId: string) => {
    try {
      const redux = await import('auth/redux');

      dispatch(redux.actionContainer.getFeedbackDetailAsync(feedbackId));
      navigationRef.navigate(SCREEN_ENUM.FeedbackDetailScreen);
    } catch (error) {
      logger('error', error);
    }
  };
  return (
    <View style={styles.container}>
      {/* Date Card */}
      <DateCard date={item.createdAt} />

      {/* Main Card */}
      <View style={styles.card}>
        <View style={styles.mainContent}>
          {/* Header */}
          <Header data={item} />

          {/* Content */}
          <View style={styles.content}>
            {/* Submitted at */}
            <View style={styles.infoRow}>
              <Text style={styles.infoLabel}>{t('SUBMITTED_AT')}</Text>
              <Text style={styles.infoValue}>
                {formatDateToMMDDYYYY(item.createdAt)}
              </Text>
            </View>

            {/* Description */}
            <View style={styles.infoRow}>
              <Text style={styles.infoLabel}>{t('DESCRIPTION')}</Text>
              <Text style={styles.descriptionValue} numberOfLines={5}>
                {item.description}
              </Text>
            </View>
          </View>
          {/* Action Buttons */}
          <Button
            variant="primary"
            style={styles.actionButton}
            onPress={() => handleGetFeedbackDetail(item.id)}
          >
            <Text style={styles.actionButtonText}>{t('VIEW_DETAILS')}</Text>
          </Button>
        </View>
      </View>
    </View>
  );
}
