import { BaseContainer, HeaderBase } from 'tradewize-component';
import React, { useRef } from 'react';
import { ScrollView, KeyboardAvoidingView, Platform } from 'react-native';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { navigationRef } from '../../services/NavigationService';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../../../Auth/src/redux/store/types';
import FeedbackInformation from './components/FeedbackInformation';
import RespondSection, { RespondSectionRef } from './components/RespondSection';
import CommentInput from './components/CommentInput';
import { styles } from './styles';
import { submitFeedbackResponseAsync } from '../../../../Auth/src/redux/features/feedback/actions';
import { logger } from '../../../../Auth/src/utils';

export default function FeedbackDetailScreen() {
  const { t } = useAppTranslation();
  const insets = useSafeAreaInsets();
  const scrollViewRef = useRef<ScrollView>(null);
  const respondSectionRef = useRef<RespondSectionRef>(null);
  const dispatch = useDispatch();

  const { feedbackDetail } = useSelector((state: RootState) => state.feedback);

  const handleSendComment = async (
    comment: string,
    images?: { uri: string; path: string; mime: string; name?: string }[],
  ) => {
    try {
      if (!feedbackDetail?.id) {
        logger('No feedback ID available');
        return;
      }

      // Submit feedback response - saga will handle S3 upload
      dispatch(
        submitFeedbackResponseAsync({
          feedbackId: feedbackDetail.id,
          message: comment,
          images: images,
          onSuccess: () => {
            setTimeout(() => {
              respondSectionRef.current?.scrollToEnd();
            }, 500);
          },
          onError: error => {
            logger('Error submitting comment:', error);
          },
        }),
      );
    } catch (error) {
      logger('Error in handleSendComment:', error);
    }
  };

  const handleInputFocus = () => {
    // Scroll to bottom when input is focused
    setTimeout(() => {
      scrollViewRef.current?.scrollToEnd({ animated: true });
      // Also scroll responses to latest message
      respondSectionRef.current?.scrollToEnd();
    }, 100);
  };

  if (!feedbackDetail) {
    return null;
  }

  const keyboardVerticalOffset =
    Platform.OS === 'ios' ? insets.bottom + 10 : insets.bottom + 20;

  return (
    <BaseContainer>
      <HeaderBase
        title={t('FEEDBACK_DETAIL')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />
      <KeyboardAvoidingView
        style={styles.keyboardAvoidingView}
        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
        keyboardVerticalOffset={keyboardVerticalOffset}
      >
        <ScrollView
          ref={scrollViewRef}
          style={styles.container}
          contentContainerStyle={styles.scrollContent}
          showsVerticalScrollIndicator={false}
          keyboardShouldPersistTaps="handled"
        >
          <FeedbackInformation
            feedbackIndex={feedbackDetail.feedbackIndex}
            status={feedbackDetail.status}
            description={feedbackDetail.description}
            feedbackType={feedbackDetail.feedbackType}
            createdAt={feedbackDetail.createdAt}
            attachments={feedbackDetail.attachments || []}
          />

          <RespondSection
            ref={respondSectionRef}
            feedbackResponses={feedbackDetail.feedbackResponses}
          />
        </ScrollView>

        <CommentInput
          onSendComment={handleSendComment}
          onFocus={handleInputFocus}
        />
      </KeyboardAvoidingView>
    </BaseContainer>
  );
}
