import {
  BaseContainer,
  HeaderBase,
  SCREEN_WIDTH,
  Button,
  Text,
} from 'tradewize-component';
import React, { useMemo } from 'react';
import { TabView } from 'react-native-tab-view';
import ScreenTab, { renderTabBar } from './components/ScreenTab';
import { navigationRef } from '../../services/NavigationService';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../theme';
import { IconFilter } from '../../assets/svg';
import FilterBottomSheet from './components/Filter';
import { useFeedback } from '../../hooks/useFeedback';
import { styles } from './styles';
import { SCREEN_ENUM } from '../../types/global';

export default function FeedbackListScreen() {
  const { t } = useAppTranslation();

  // Use custom hook for all feedback-related logic
  const {
    index,
    setIndex,
    // filterValues,
    filterBottomSheetRef,
    pendingList,
    completedList,
    handleApplyFilter,
    handleOpenFilter,
    feedbackTypeOptions,
  } = useFeedback();

  const routes = useMemo(
    () => [
      { key: 'pending', title: t('PENDING') || 'Pending' },
      { key: 'completed', title: t('COMPLETED') || 'Completed' },
    ],
    [t],
  );

  const renderScene = ({ route }: { route: { key: string } }) => {
    switch (route.key) {
      case 'pending':
        return <ScreenTab data={pendingList} />;
      case 'completed':
        return <ScreenTab data={completedList} />;
      default:
        return null;
    }
  };

  return (
    <>
      <BaseContainer>
        <HeaderBase
          title={t('FEEDBACK')}
          onPressBack={() => {
            navigationRef.goBack();
          }}
          rightActions={[
            {
              icon: <SvgXml xml={IconFilter(colors.mainColorPrimary)} />,
              onPress: handleOpenFilter,
            },
          ]}
        />
        <TabView
          lazy
          navigationState={{ index, routes }}
          renderScene={renderScene}
          onIndexChange={setIndex}
          renderTabBar={renderTabBar}
          initialLayout={{ width: SCREEN_WIDTH }}
        />
        <Button
          variant="ghost"
          style={styles.addFeedbackButton}
          onPress={() => {
            navigationRef.navigate(SCREEN_ENUM.FeedbackAddScreen);
          }}
        >
          <Text style={styles.addFeedbackButtonText}>{t('ADD_FEEDBACK')}</Text>
        </Button>
      </BaseContainer>

      {/* Filter Bottom Sheet Modal */}
      <FilterBottomSheet
        ref={filterBottomSheetRef}
        onApply={handleApplyFilter}
        initialValues={{ status: null, feedbackType: null }}
        feedbackTypeOptions={feedbackTypeOptions}
      />
    </>
  );
}
