import { useState, useCallback, useEffect, useMemo, useRef } from 'react';
import { Platform } from 'react-native';
import { ChecklistItem } from '../../types/jobDetail.type';
import { MediaFile } from './types';
import BottomSheet from '@gorhom/bottom-sheet';
import { useAppSelector, useAppDispatch } from '../../../../Auth/src/redux/store/types';
import { isPassValidateFilesByFileSetting } from '../../helper/checkList.helpter';
import { generateId } from '../../helper';
import {
  hideAlertAsync,
  showAlertAsync,
  hideCommentChecklistModal,
} from '../../../../Auth/src/redux/features/app/actions';
import { getCommentChecklistAsync } from '../../../../Auth/src/redux/features/jobDetail/actions';
import { FileData } from '../../../../Auth/src/redux/features/jobDetail/types';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import ImagePicker from 'react-native-image-crop-picker';
import { Response as ResponseItem } from '../../types/jobDetail.type';
import { logger } from '../../utils';

export const useCommentChecklist = () => {
  const { item, visible, readonly, isLoadingUpload, onClose } = useAppSelector(
    state => state.app.commentChecklistModal,
  );
  const dispatch = useAppDispatch();
  const { t } = useAppTranslation();

  const bottomSheetRef = useRef<BottomSheet>(null);
  const [localItem, setLocalItem] = useState<ResponseItem | null>(null);
  const [isLoadingPhoto, setIsLoadingPhoto] = useState(false);
  const [isLoadingVideo, setIsLoadingVideo] = useState(false);
  const [isOpenCamera, setIsOpenCamera] = useState(false);
  const [typeCamera, setTypeCamera] = useState<'photo' | 'video'>('photo');
  const [itemPreview, setItemPreview] = useState<string>('');
  const [isModalPreview, setIsModalPreview] = useState(false);
  const [isModalVideo, setIsModalVideo] = useState(false);

  const isLastCommentFromOthers = useMemo(() => {
    const lastResponse = item?.responses?.at(-1);
    return !!lastResponse && lastResponse.isExMyResponse === false;
  }, [item?.responses]);

  // Check if there are required fields (fileSettings with minFiles > 0)
  const hasRequiredFields = useMemo(() => {
    if (!item?.fileSettings || !Array.isArray(item.fileSettings)) {
      return false;
    }
    return item.fileSettings.some((setting: any) => setting.minFiles > 0);
  }, [item?.fileSettings]);

  // Dynamic snapPoints based on required fields
  const snapPoints = useMemo(() => {
    return ['25%', '50%', '95%'];
  }, [hasRequiredFields]);

  const shouldUseDynamicSizing = useMemo(() => {
    return !hasRequiredFields;
  }, [hasRequiredFields]);

  // Reset state when visibility changes
  useEffect(() => {
    if (visible && item) {
      setLocalItem({
        responseId: generateId(),
        comment: '',
        fileEntities: [],
        isExMyResponse: true,
        createdBy: '',
        createdAt: new Date().toISOString(),
        createdName: '',
        fileExtension: '',
      } as unknown as ResponseItem);

      setIsLoadingPhoto(false);
      setIsLoadingVideo(false);
    } else if (!visible) {
      setLocalItem(null);
      setIsLoadingPhoto(false);
      setIsLoadingVideo(false);
    }
  }, [visible, item]);

  // Control bottom sheet visibility
  useEffect(() => {
    if (visible) {
      setTimeout(() => {
        bottomSheetRef.current?.expand();
      }, 50);
    } else {
      bottomSheetRef.current?.close();
    }
  }, [visible]);

  // Cleanup on unmount - close modal and reset state
  useEffect(() => {
    return () => {
      // Force close bottom sheet
      bottomSheetRef.current?.close();
      // Reset Redux state to ensure modal is hidden when returning to screen
      dispatch(hideCommentChecklistModal());
    };
  }, [dispatch]);

  const handleSheetChanges = useCallback(
    (index: number) => {
      if (index === -1) {
        setLocalItem(null);
        setIsLoadingPhoto(false);
        setIsLoadingVideo(false);
        onClose?.();
      }
    },
    [onClose],
  );

  const normalizePath = (path: string) =>
    Platform.OS === 'android' && path && !path.startsWith('file://')
      ? `file://${path}`
      : path;

  const handleMediaCaptured = useCallback(
    async (media: any) => {
      if (!media) return;
      setIsOpenCamera(false);

      const isPhoto = media.mime && media.mime.startsWith('image/');

      // Video duration validation
      if (!isPhoto) {
        try {
          let durationInSeconds = 0;
          let rawDuration = 0;

          if (media.duration) {
            rawDuration = Number(media.duration);
            // Heuristic: If duration is small, treat as seconds. If large, treat as ms.
            if (rawDuration < 100) {
              durationInSeconds = rawDuration;
            } else {
              durationInSeconds = rawDuration / 1000;
            }
          }

          // Min 3s, Max 60s
          if (durationInSeconds < 2 || durationInSeconds > 60) {
            dispatch(
              showAlertAsync({
                visible: true,
                title: t('WARNING'),
                message: t('VIDEO_DURATION_ERROR', { min: 3, max: 60 }),
                type: 'warning',
                buttonText: t('CLOSE'),
                onClose: () => {
                  dispatch(hideAlertAsync());
                },
              }),
            );
            return;
          }
        } catch (e) {
          logger('Validation error', e);
          return;
        }
      }

      // Set loading state briefly for UI feedback
      isPhoto ? setIsLoadingPhoto(true) : setIsLoadingVideo(true);

      try {
        const finalUri = normalizePath(media.path || '');
        const fileName =
          finalUri?.replace('file://', '')?.split('/').pop() ||
          `media_${Date.now()}.${isPhoto ? 'jpg' : 'mp4'}`;

        // Normalize duration to milliseconds for storage
        let finalDurationMs = 0;
        const rDuration = Number(media.duration);
        if (rDuration < 100) {
          finalDurationMs = rDuration * 1000;
        } else {
          finalDurationMs = rDuration;
        }

        const mediaData: MediaFile = {
          uri: finalUri,
          type: media.mime || (isPhoto ? 'image/jpeg' : 'video/mp4'),
          fileName,
          fileSize: media.size || 0,
          duration: finalDurationMs,
          width: media.width,
          height: media.height,
          thumbnailPreview: finalUri,
        };

        // Add to local state - S3 upload will be handled by saga on save
        setLocalItem((prev: any) => {
          const currentFiles = prev?.fileEntities || [];
          return {
            ...prev,
            fileEntities: [...currentFiles, mediaData],
          };
        });
      } catch (error) {
        logger('Media capture error:', error);
      } finally {
        isPhoto ? setIsLoadingPhoto(false) : setIsLoadingVideo(false);
      }
    },
    [dispatch, t],
  );

  const handleLaunchCamera = useCallback(
    async (type: 'photo' | 'video') => {
      try {
        const image = await ImagePicker.openCamera({
          cropping: false,
          compressImageQuality: 0.8,
          compressImageMaxWidth: 1280,
          compressImageMaxHeight: 1280,
          includeBase64: false,
          mediaType: type,
          forceJpg: true,
        });

        const mediaItem = {
          path: image.path,
          size: image.size,
          duration: (image as any).duration || 0,
          width: image.width,
          height: image.height,
          mime: image.mime,
        };

        handleMediaCaptured(mediaItem);
      } catch (error) {
        logger('Error opening camera', error);
      }
    },
    [handleMediaCaptured],
  );

  const showMediaOptions = useCallback(
    (type: 'photo' | 'video') => {
      handleLaunchCamera(type);
    },
    [handleLaunchCamera],
  );

  const handleRemoveMedia = useCallback((media: any) => {
    setLocalItem((prev: any) => {
      if (!prev) return null;
      const currentFiles = prev.fileEntities || [];
      const newFiles = currentFiles.filter(
        (item: any) => item?.fileName !== media?.fileName,
      );
      return {
        ...prev,
        fileEntities: newFiles,
      };
    });
  }, []);

  const handleCommentChange = useCallback((text: string) => {
    setLocalItem((prev: any) => {
      if (!prev) return { comment: text, fileEntities: [] };
      return {
        ...prev,
        comment: text,
      };
    });
  }, []);

  const handlePreviewMedia = useCallback((media: any) => {
    setItemPreview(media?.uri);
    if (media?.type?.includes('video')) {
      setIsModalVideo(true);
    } else {
      setIsModalPreview(true);
    }
  }, []);

  const closePreview = useCallback(() => {
    setIsModalPreview(false);
    setIsModalVideo(false);
    setItemPreview('');
  }, []);

  const handleSave = useCallback(() => {
    // Construct a validation object that mimics ChecklistItem structure for the helper
    const itemToValidate = {
      ...item,
      fileEntities: localItem?.fileEntities || [],
      comment: localItem?.comment,
    } as ChecklistItem;

    if (localItem && item) {
      const fileEntities = (localItem as any)?.fileEntities || [];
      const localItemFiles = fileEntities.filter((file: any) =>
        file.type?.includes('image'),
      );
      const localItemVideos = fileEntities.filter((file: any) =>
        file.type?.includes('video'),
      );
      const hasComment =
        (localItem as any)?.comment &&
        (localItem as any).comment.trim().length > 0;
      const hasPhotos = localItemFiles.length > 0;
      const hasVideos = localItemVideos.length > 0;

      // Validate: need at least comment OR photo OR video
      if (!hasComment && !hasPhotos && !hasVideos) {
        dispatch(
          showAlertAsync({
            visible: true,
            title: t('WARNING'),
            message: t('NEED_AT_LEAST_ONE_INPUT'),
            type: 'warning',
            buttonText: t('CLOSE'),
            onClose: () => {
              dispatch(hideAlertAsync());
            },
          }),
        );
        return;
      }

      if (!isPassValidateFilesByFileSetting(itemToValidate)) {
        dispatch(
          showAlertAsync({
            visible: true,
            title: t('WARNING'),
            message: t('CHECK_REQUIRED_FILES'),
            type: 'warning',
            buttonText: t('CLOSE'),
            onClose: () => {
              dispatch(hideAlertAsync());
            },
          }),
        );
        return;
      }

      // Convert MediaFile[] to FileData[] format for saga (S3 upload handled there)
      const fileDatas: FileData[] = fileEntities.map((file: MediaFile) => ({
        fileName: file.fileName,
        uri: file.uri,
        path: file.uri,
        mime: file.type,
        size: file.fileSize,
      }));

      // Dispatch action - saga will handle S3 upload and API call
      dispatch(
        getCommentChecklistAsync({
          id: item.id,
          isChecked: item.isChecked ?? undefined,
          comment: localItem?.comment || '',
          fileDatas: fileDatas.length > 0 ? fileDatas : [],
          onSuccess: () => {
            dispatch(hideCommentChecklistModal());
          },
          onError: (error: any) => {
            logger('Submit comment error:', error);
          },
        }),
      );
    }
  }, [localItem, item, dispatch, t]);

  const getMediaList = useCallback(
    (type: 'photo' | 'video') => {
      const fileEntities = (localItem as any)?.fileEntities || [];
      return fileEntities.filter((file: any) =>
        file.type?.includes(type === 'photo' ? 'image' : 'video'),
      );
    },
    [localItem],
  );

  return {
    bottomSheetRef,
    localItem,
    isLoadingPhoto,
    isLoadingVideo,
    isOpenCamera,
    typeCamera,
    itemPreview,
    isModalPreview,
    isModalVideo,
    snapPoints,
    shouldUseDynamicSizing,
    handleSheetChanges,
    showMediaOptions,
    handleMediaCaptured,
    handleRemoveMedia,
    handleCommentChange,
    handlePreviewMedia,
    closePreview,
    handleSave,
    getMediaList,
    setIsOpenCamera,
    readonly,
    isLoadingUpload,
    item,
    visible,
    isLastCommentFromOthers,
  };
};
