import { useState, useEffect, useMemo, useRef, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import type {
  FilterValues,
  FilterBottomSheetRef,
} from '../screens/FeedbackListScreen/components/Filter';
import { BottomSelectorOption } from 'tradewize-component';

export const useFeedback = () => {
  const dispatch = useDispatch();
  const [index, setIndex] = useState(0);
  const filterBottomSheetRef = useRef<FilterBottomSheetRef>(null);

  const feedbackTypeData = useSelector(
    (state: any) => state.feedback?.feedbackType || [],
  );

  // Feedback type options from API - already in correct format
  const feedbackTypeOptions: BottomSelectorOption[] = useMemo(
    () => feedbackTypeData,
    [feedbackTypeData],
  );

  // Get data from Redux
  const pendingList = useSelector(
    (state: any) => state.feedback?.pendingList || [],
  );
  const completedList = useSelector(
    (state: any) => state.feedback?.completedList || [],
  );

  const fetchGetFeedbackList = useCallback(
    async (status?: string, feedbackType?: string) => {
      try {
        const redux = await import('auth/redux');
        dispatch(
          redux.actionContainer.getFeedbackListAsync({
            feedbackStatusParam: status,
            feedbackTypeIdParam: feedbackType,
          }),
        );
      } catch (error) {
        console.log(error);
      }
    },
    [dispatch],
  );

  const fetchGetFeedbackType = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getFeedbackTypeAsync());
    } catch (error) {
      console.log(error);
    }
  }, [dispatch]);

  // Fetch feedback list and types on mount
  useEffect(() => {
    fetchGetFeedbackList();
    fetchGetFeedbackType();
  }, [fetchGetFeedbackList, fetchGetFeedbackType]);

  // Handlers
  const handleApplyFilter = (filters: FilterValues) => {
    fetchGetFeedbackList(filters.status?.value, filters.feedbackType?.value);
  };

  const handleOpenFilter = () => {
    filterBottomSheetRef.current?.open();
  };

  return {
    // State
    index,
    setIndex,
    filterBottomSheetRef,

    // Data
    pendingList,
    completedList,

    // Handlers
    handleApplyFilter,
    handleOpenFilter,

    // Options for filter
    feedbackTypeOptions,
  };
};
