import React, { useState } from 'react';
import { View, Image } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { IconChevronUp, IconChevronDown } from '../../../../assets/svg';
import { colors } from '../../../../theme';
import { styles } from './styles';
import StatusBadge from '../StatusBadge';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { formatDateToMMDDYYYY } from '../../../../helper/FormatDate.helper';
import { FeedbackInformationProps } from '../../../../types/FeedBack';

export default function FeedbackInformation({
  feedbackIndex,
  status,
  description,
  feedbackType,
  createdAt,
  attachments,
}: FeedbackInformationProps) {
  const { t } = useAppTranslation();
  const [isExpanded, setIsExpanded] = useState(true);

  return (
    <View style={styles.container}>
      {/* Header */}
      <Button
        style={styles.header}
        onPress={() => setIsExpanded(!isExpanded)}
        activeOpacity={0.7}
      >
        <Text style={styles.headerTitle}>{t('FEEDBACK_INFORMATION')}</Text>
        <SvgXml
          xml={
            isExpanded
              ? IconChevronUp(colors.grayScale100)
              : IconChevronDown(colors.grayScale100)
          }
          width={24}
          height={24}
        />
      </Button>

      {/* Content */}
      {isExpanded && (
        <View style={styles.content}>
          <View style={styles.divider} />

          {/* Status */}
          <View style={styles.row}>
            <Text style={styles.label}>{t('STATUS')}</Text>
            <StatusBadge status={status} />
          </View>

          {/* Feedback No */}
          <View style={styles.row}>
            <Text style={styles.label}>{t('FEEDBACK_NO')}</Text>
            <Text style={styles.value}>{feedbackIndex || '-'}</Text>
          </View>

          {/* Description */}
          <View style={styles.row}>
            <Text style={styles.label}>{t('DESCRIPTION')}:</Text>
            <Text style={styles.value}>{description || '-'}</Text>
          </View>

          {/* Attachments */}
          {attachments && attachments?.length > 0 && (
            <View style={styles.row}>
              <Text style={styles.label}>{t('ATTACHMENTS')}</Text>
              <View style={styles.attachmentsContainer}>
                {attachments?.map((attachment, index) => (
                  <Button
                    key={index}
                    style={styles.attachmentItem}
                    activeOpacity={0.7}
                  >
                    <Image
                      source={{ uri: attachment?.url }}
                      style={styles.attachmentImage}
                      resizeMode="cover"
                    />
                  </Button>
                ))}
              </View>
            </View>
          )}

          {/* Feedback Type */}
          <View style={styles.row}>
            <Text style={styles.label}>{t('FEEDBACK_TYPE')}</Text>
            <Text style={styles.valueBlue}>{feedbackType || '-'}</Text>
          </View>

          {/* Target Date */}
          <View style={styles.row}>
            <Text style={styles.label}>{t('TARGET_DATE')}</Text>
            <Text style={styles.value}>
              {formatDateToMMDDYYYY(createdAt) || '-'}
            </Text>
          </View>
        </View>
      )}
    </View>
  );
}
