import { Text, View } from 'react-native';
import { styles } from './styles';
import { getWorkerScheduleActivityAsync } from '../../../../Auth/src/redux/features/jobBoard/actions';
import { useDispatch, useSelector } from 'react-redux';
import { useCallback, useEffect } from 'react';
import { useRoute } from '@react-navigation/native';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { NavigationService } from '../../services/NavigationService';
import { FlashList } from '@shopify/flash-list';
import FastImage from 'react-native-fast-image';
import { logger } from '../../../../Auth/src/utils';

interface MemberItem {
  purchaseOrderId: string;
  companyName: string;
  id: any;
  companyRole: CompanyRole;
  userName: any;
  firstName: any;
  lastName: any;
  avatar: string | null;
  userRating: any;
  pORating: any;
  avgRating: any;
}

interface CompanyRole {
  id: string;
  name: string;
  code: string;
  roleType: number;
  roleTypeName: string;
}

const DEFAULT_AVATAR =
  'https://ui-avatars.com/api/?background=0D8ABC&color=fff';

const ListMemberJobsBoard = () => {
  const { t } = useAppTranslation();
  const { scheduleActivityId } = useRoute().params as {
    scheduleActivityId: string;
  };

  const dispatch = useDispatch();
  const workerScheduleActivity: MemberItem[] = useSelector(
    (state: any) => state.jobBoard.workerScheduleActivity.data,
  );

  const fetchWorkerScheduleActivity = useCallback(() => {
    dispatch(getWorkerScheduleActivityAsync(scheduleActivityId));
  }, [dispatch, scheduleActivityId]);

  useEffect(() => {
    fetchWorkerScheduleActivity();
  }, [fetchWorkerScheduleActivity]);

  const handleGoBack = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      NavigationService.goBack();
      dispatch(redux.actionContainer.clearWorkerScheduleActivityState());
    } catch (error) {
      logger('error handleGoBack', error);
    }
  }, [dispatch]);

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase title={t('MEMBERS')} onPressBack={handleGoBack} />
      <View style={styles.contentContainer}>
        {workerScheduleActivity && (
          <Text style={styles.totalMembersText}>
            {`${
              workerScheduleActivity?.filter(
                (member: any) => member.firstName && member.lastName,
              ).length
            }/${workerScheduleActivity?.length} ${t('MEMBERS')}`}
          </Text>
        )}
        <FlashList
          style={styles.flashListContainer}
          data={workerScheduleActivity}
          renderItem={({ item }) => {
            const displayName =
              item.firstName && item.lastName
                ? `${item.firstName} ${item.lastName}`
                : `Unassigned - ${item.companyRole?.name}`;
            return (
              <View style={styles.memberItemContainer}>
                {item?.avatar ? (
                  <FastImage
                    source={{ uri: item?.avatar }}
                    style={styles.avatar}
                    resizeMode={FastImage.resizeMode.contain}
                  />
                ) : (
                  <FastImage
                    source={{ uri: DEFAULT_AVATAR }}
                    style={styles.avatar}
                    resizeMode={FastImage.resizeMode.contain}
                  />
                )}
                {displayName && (
                  <Text numberOfLines={1} style={styles.memberNameText}>
                    {displayName}
                  </Text>
                )}
              </View>
            );
          }}
          keyExtractor={(item: MemberItem, index: number) => `
            ${item?.purchaseOrderId}-${index}`}
        />
      </View>
    </BaseContainer>
  );
};

export default ListMemberJobsBoard;
