import React from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { styles } from './styles';
import { FeedbackStatus } from '../../../../types/FeedBack';

interface StatusBadgeProps {
  status: FeedbackStatus;
}

export default function StatusBadge({ status }: StatusBadgeProps) {
  const { t } = useAppTranslation();

  const getStatusInfo = (s: FeedbackStatus) => {
    switch (s) {
      case FeedbackStatus.IN_REVIEW:
        return {
          label: t('IN_REVIEW'),
          style: styles.inReview,
        };
      case FeedbackStatus.IN_THE_WORKS:
        return {
          label: t('IN_THE_WORKS'),
          style: styles.inTheWorks,
        };
      case FeedbackStatus.COMPLETED:
        return {
          label: t('COMPLETED'),
          style: styles.completed,
        };
      case FeedbackStatus.PAST_DUE:
        return {
          label: t('PAST_DUE'),
          style: styles.pastDue,
        };
      case FeedbackStatus.NO_ACTION:
        return {
          label: t('NO_ACTION'),
          style: styles.noAction,
        };
      default:
        return {
          label: t('IN_REVIEW'),
          style: styles.inReview,
        };
    }
  };

  const statusInfo = getStatusInfo(status);

  return (
    <View style={[styles.badge, statusInfo.style]}>
      <Text style={[styles.badgeText, statusInfo.style]}>
        {statusInfo.label}
      </Text>
    </View>
  );
}
