import React, { forwardRef, useImperativeHandle } from 'react';
import { View, ScrollView, TouchableOpacity } from 'react-native';
import Modal from 'react-native-modal';
import { Text, Button, BottomSelector } from 'tradewize-component';
import type { BottomSelectorOption } from 'tradewize-component';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { styles } from './styles';
import { IconClose, IconChevronDown } from '../../../../assets/svg';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../../../theme';
import { getStatusOptions } from '../../../../helper/Feedback';

export interface FilterValues {
  status: BottomSelectorOption | null;
  feedbackType: BottomSelectorOption | null;
}

export interface FilterBottomSheetRef {
  open: () => void;
  close: () => void;
}

interface FilterBottomSheetProps {
  onApply: (filters: FilterValues) => void;
  initialValues?: FilterValues;
  feedbackTypeOptions: BottomSelectorOption[];
}

const FilterBottomSheet = forwardRef<
  FilterBottomSheetRef,
  FilterBottomSheetProps
>(({ onApply, initialValues, feedbackTypeOptions }, ref) => {
  const { t } = useAppTranslation();
  const [isVisible, setIsVisible] = React.useState(false);
  const [isStatusSelectorVisible, setIsStatusSelectorVisible] =
    React.useState(false);
  const [isFeedbackTypeSelectorVisible, setIsFeedbackTypeSelectorVisible] =
    React.useState(false);

  // State for filter values
  const [status, setStatus] = React.useState<BottomSelectorOption | null>(
    initialValues?.status || null,
  );
  const [feedbackType, setFeedbackType] =
    React.useState<BottomSelectorOption | null>(
      initialValues?.feedbackType || null,
    );

  // Expose open/close methods to parent
  useImperativeHandle(ref, () => ({
    open: () => {
      setIsVisible(true);
    },
    close: () => {
      setIsVisible(false);
    },
  }));

  // Handle apply filter
  const handleApply = () => {
    onApply({ status, feedbackType });
    setIsVisible(false);
  };

  // Handle reset filter
  const handleReset = () => {
    setStatus(null);
    setFeedbackType(null);
    onApply({ status: null, feedbackType: null });
  };

  // Handle close modal
  const handleClose = () => {
    setIsVisible(false);
  };

  // Status options

  return (
    <Modal
      isVisible={isVisible}
      // onBackdropPress={handleClose}
      onSwipeComplete={handleClose}
      swipeDirection={['down']}
      style={styles.modal}
      backdropOpacity={0.5}
      animationIn="slideInUp"
      animationOut="slideOutDown"
      useNativeDriver
      hideModalContentWhileAnimating
    >
      <View style={styles.modalContainer}>
        {/* Handle Indicator */}
        <View style={styles.handleIndicatorContainer}>
          <Button
            variant="ghost"
            onPress={handleClose}
            style={styles.closeButton}
          >
            <SvgXml
              xml={IconClose(colors.grayScale70)}
              width={24}
              height={24}
            />
          </Button>
        </View>

        <ScrollView
          style={styles.bottomSheetContent}
          contentContainerStyle={styles.scrollContent}
          showsVerticalScrollIndicator={false}
          bounces={false}
        >
          {/* Filter Options */}
          <View style={styles.filterBody}>
            {/* Feedback Type Selector */}
            <View style={styles.selectorContainer}>
              <Text style={styles.selectorLabel}>{t('FEEDBACK_TYPE')}</Text>
              <TouchableOpacity
                style={styles.selectorButton}
                onPress={() => setIsFeedbackTypeSelectorVisible(true)}
                activeOpacity={0.7}
              >
                <Text
                  style={[
                    styles.selectorButtonText,
                    feedbackType && styles.selectorButtonTextSelected,
                  ]}
                >
                  {feedbackType?.label || t('SELECT_FEEDBACK_TYPE')}
                </Text>
                <SvgXml
                  xml={IconChevronDown(colors.grayScale100)}
                  width={20}
                  height={20}
                />
              </TouchableOpacity>
            </View>

            {/* Status Selector */}
            <View style={styles.selectorContainer}>
              <Text style={styles.selectorLabel}>
                {t('STATUS') || 'Status'}
              </Text>
              <TouchableOpacity
                style={styles.selectorButton}
                onPress={() => setIsStatusSelectorVisible(true)}
                activeOpacity={0.7}
              >
                <Text
                  style={[
                    styles.selectorButtonText,
                    status && styles.selectorButtonTextSelected,
                  ]}
                >
                  {status?.label || t('SELECT_STATUS') || 'Select status'}
                </Text>
                <SvgXml
                  xml={IconChevronDown(colors.grayScale100)}
                  width={20}
                  height={20}
                />
              </TouchableOpacity>
            </View>
          </View>
        </ScrollView>

        {/* Action Buttons - Fixed at Bottom */}
        <View style={styles.filterFooter}>
          <Button
            variant="secondary"
            style={styles.resetButton}
            onPress={handleReset}
          >
            <Text style={styles.resetButtonText}>{t('RESET')}</Text>
          </Button>

          <Button
            variant="primary"
            style={styles.applyButton}
            onPress={handleApply}
          >
            <Text style={styles.applyButtonText}>{t('APPLY')}</Text>
          </Button>
        </View>
      </View>

      {/* Feedback Type Bottom Selector */}
      <BottomSelector
        title={t('SELECT_FEEDBACK_TYPE')}
        isVisible={isFeedbackTypeSelectorVisible}
        onClose={() => setIsFeedbackTypeSelectorVisible(false)}
        selectedValue={feedbackType?.value}
        options={feedbackTypeOptions}
        onSelect={option => {
          setFeedbackType(option);
          setIsFeedbackTypeSelectorVisible(false);
        }}
        containerStyle={styles.feedbackTypeSelectorContainer}
        cancelText={t('CLOSE')}
        emptyText={t('NO_DATA_AVAILABLE')}
      />

      {/* Status Bottom Selector */}
      <BottomSelector
        title={t('SELECT_STATUS')}
        isVisible={isStatusSelectorVisible}
        onClose={() => setIsStatusSelectorVisible(false)}
        selectedValue={status?.value}
        options={getStatusOptions(t)}
        onSelect={option => {
          setStatus(option);
          setIsStatusSelectorVisible(false);
        }}
        containerStyle={styles.feedbackTypeSelectorContainer}
        cancelText={t('CLOSE')}
        emptyText={t('NO_DATA_AVAILABLE')}
      />
    </Modal>
  );
});

FilterBottomSheet.displayName = 'FilterBottomSheet';

export default FilterBottomSheet;
