export interface FeedbackItem {
  feedbackIndex: number;
  pageIssue: string;
  description: string;
  actionType: number | null;
  status: number;
  feedbackTypeId: string;
  feedbackType: string;
  targetDate: string | null;
  createdById: string;
  createdByName: string;
  createdByEmail: string;
  createdByUserTypes: number[];
  createdAt: string;
  completedById: string | null;
  completedByEmail: string | null;
  completedByName: string | null;
  completedByUserTypes: number[] | null;
  completedDate: string | null;
  feedbackAssignment: FeedbackAssignment[];
  owners: any;
  attachments: FeedbackAttachment[];
  id: string;
}

export interface FeedbackAttachment {
  url: string;
  attachmentId: string;
  fileId: string;
  fileName: string;
  fileLength: number;
  fileExtension: string;
  key: string;
}

export interface FeedbackAssignment {
  accountId: string;
  accountName: string;
  accountEmail: string;
}

export interface FeedbackDetail {
  feedbackResponses: any;
  feedbackIndex: number;
  pageIssue: string;
  description: string;
  actionType: number | null;
  status: FeedbackStatus;
  feedbackTypeId: string;
  feedbackType: string;
  targetDate: string | null;
  createdById: string;
  createdByName: string;
  createdByEmail: string;
  createdByUserTypes: number[];
  createdAt: string;
  completedById: string | null;
  completedByEmail: string | null;
  completedByName: string | null;
  completedByUserTypes: number[] | null;
  completedDate: string | null;
  feedbackAssignment: FeedbackAssignment[];
  owners: any;
  attachments: FeedbackAttachment[];
  translations: {
    [key: string]: {
      feedbackType: string;
    };
  };
  id: string;
}
export enum FeedbackStatus {
  IN_REVIEW = 0,
  IN_THE_WORKS = 1,
  COMPLETED = 2,
  PAST_DUE = 3,
  NO_ACTION = 4,
}

export interface FeedbackResponse {
  responseId: string;
  time: string;
  name: string;
  accountId: string;
  comment: string;
  attachments: FeedbackAttachment[];
  userTypes: number[];
}

export interface FeedbackInformationProps {
  feedbackIndex: number;
  status: number;
  description: string;
  feedbackType: string;
  createdAt: string | null;
  attachments: Array<{
    url?: string;
    fileName?: string;
  }>;
}

export interface FeedbackAttachment {
  attachmentId: string;
  fileId: string;
  fileName: string;
  fileLength: number;
  fileExtension: string;
  key: string;
  url: string;
}

export interface SelectedMedia {
  uri: string;
  path: string;
  mime: string;
  type: 'image' | 'video';
}
