import { useState, useMemo, useCallback } from 'react';
import { Alert } from 'react-native';
import { useDispatch, useSelector } from 'react-redux';
import type { BottomSelectorOption } from 'tradewize-component';
import { getPageIssueOptions } from '../helper/Feedback';
import ImagePicker from 'react-native-image-crop-picker';
import { SelectedMedia } from '../types/FeedBack';
import { useAppTranslation } from './useAppTranslation';
import { navigationRef } from '../services/NavigationService';
import { logger } from '../utils';

const MAX_MEDIA = 5;

export const useFeedbackAdd = () => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();

  const [feedbackType, setFeedbackType] = useState<BottomSelectorOption | null>(
    null,
  );
  const [isFeedbackTypeSelectorVisible, setIsFeedbackTypeSelectorVisible] =
    useState(false);
  const [pageIssue, setPageIssue] = useState<BottomSelectorOption | null>(null);
  const [isPageIssueSelectorVisible, setIsPageIssueSelectorVisible] =
    useState(false);
  const [description, setDescription] = useState('');
  const [attachments, setAttachments] = useState<SelectedMedia[]>([]);

  const feedbackTypeOptions: BottomSelectorOption[] = useSelector(
    (state: any) => state.feedback?.feedbackType || [],
  );
  const { feedbackTypeFullList, feedbackFilter } = useSelector(
    (state: any) => state.feedback,
  );

  const accountIds = useMemo(() => {
    if (!feedbackType) return [];
    const feedbackTypeItem = feedbackTypeFullList.find(
      (item: any) => item.id === feedbackType.value,
    );
    return feedbackTypeItem?.owners?.map((owner: any) => owner.id) || [];
  }, [feedbackType, feedbackTypeFullList]);

  const pageIssueOptions = useMemo(() => getPageIssueOptions(t), [t]);

  const handleSelectFromGallery = useCallback(async () => {
    try {
      if (attachments.length >= MAX_MEDIA) {
        Alert.alert(
          t('WARNING'),
          t('MAXIMUM_IMAGES_REACHED', { max: MAX_MEDIA }),
        );
        return;
      }

      const remainingSlots = MAX_MEDIA - attachments.length;

      const media = await ImagePicker.openPicker({
        multiple: true,
        maxFiles: remainingSlots,
        mediaType: 'any',
        cropping: false,
      });

      const newMedia = (Array.isArray(media) ? media : [media]).map(
        (item: any): SelectedMedia => ({
          uri: item?.path,
          path: item?.path,
          mime: item?.mime,
          type: item?.mime?.startsWith('video/') ? 'video' : 'image',
        }),
      );

      const mediaToAdd = newMedia?.slice(0, remainingSlots);

      if (newMedia?.length > remainingSlots) {
        Alert.alert(
          t('WARNING'),
          `${t('MAXIMUM_IMAGES_REACHED', { max: MAX_MEDIA })}\nAdded ${
            mediaToAdd?.length
          } of ${newMedia?.length} selected items.`,
        );
      }

      setAttachments(prev => [...(prev || []), ...(mediaToAdd || [])]);
    } catch (error) {
      logger('User cancelled media picker', error);
    }
  }, [attachments, t]);

  const handleTakePhoto = useCallback(async () => {
    try {
      if (attachments.length >= MAX_MEDIA) {
        Alert.alert(
          t('WARNING'),
          t('MAXIMUM_IMAGES_REACHED', { max: MAX_MEDIA }),
        );
        return;
      }

      const image = await ImagePicker.openCamera({
        mediaType: 'photo',
        cropping: false,
      });

      const newMedia = {
        uri: image?.path,
        path: image?.path,
        mime: image?.mime,
        type: 'image' as const,
      };

      setAttachments(prev => [...(prev || []), newMedia]);
    } catch (error) {
      logger('User cancelled camera', error);
    }
  }, [attachments, t]);

  const handleRemoveMedia = useCallback((index: number) => {
    setAttachments(prev => prev?.filter((_, i) => i !== index) || []);
  }, []);

  const handleSubmit = useCallback(() => {
    if (!pageIssue) {
      Alert.alert(
        t('ERROR') || 'Error',
        t('PLEASE_SELECT_PAGE_ISSUE') || 'Please select page issue',
      );
      return;
    }

    if (!feedbackType) {
      Alert.alert(
        t('ERROR') || 'Error',
        t('PLEASE_SELECT_FEEDBACK_TYPE') || 'Please select feedback type',
      );
      return;
    }

    if (!description?.trim()) {
      Alert.alert(
        t('ERROR') || 'Error',
        t('PLEASE_ENTER_DESCRIPTION') || 'Please enter description',
      );
      return;
    }

    const createFeedbackAction = async () => {
      try {
        const redux = await import('auth/redux');
        dispatch(
          redux.actionContainer.createFeedbackAsync({
            feedbackTypeId: feedbackType.value as string,
            pageIssue: pageIssue.value as string,
            description: description.trim(),
            attachments: attachments,
            accountIds: accountIds,
            onSuccess: () => {
              setFeedbackType(null);
              setPageIssue(null);
              setDescription('');
              setAttachments([]);
              dispatch(
                redux.actionContainer.getFeedbackListAsync(feedbackFilter),
              );
              navigationRef.goBack();
            },
            onError: (error: any) => {
              logger('Create feedback error:', error);
            },
          }),
        );
      } catch (error) {
        logger('Error creating feedback:', error);
      }
    };

    createFeedbackAction();
  }, [
    pageIssue,
    feedbackType,
    description,
    attachments,
    accountIds,
    dispatch,
    t,
    feedbackFilter,
  ]);

  const canSubmit = !!(pageIssue && feedbackType && description?.trim());

  return {
    feedbackType,
    setFeedbackType,
    isFeedbackTypeSelectorVisible,
    setIsFeedbackTypeSelectorVisible,
    pageIssue,
    setPageIssue,
    isPageIssueSelectorVisible,
    setIsPageIssueSelectorVisible,
    description,
    setDescription,
    attachments,
    setAttachments,
    feedbackTypeOptions,
    pageIssueOptions,
    accountIds,
    canSubmit,
    handleSelectFromGallery,
    handleTakePhoto,
    handleRemoveMedia,
    handleSubmit,
  };
};
