import {
  ChecklistItem,
  FileSetting,
  FileType,
  qIStatus,
  userRole,
  VideoType,
} from '../types/jobDetail.type';

/**
 * Validates if a checklist item meets file requirements based on fileSettings.
 *
 * Logic:
 * 1. Role-based checks (INSPECTOR status, leaderCheckIncomplete)
 * 2. Check fileSettings requirements
 * 3. Count files from user's responses + local files
 * 4. If last response is NOT from current user  user needs to add new files
 * 5. Validate total files meet min/max requirements
 */
export const isPassValidateFilesByFileSetting = (
  checklistItem: ChecklistItem | null | undefined,
): boolean => {
  if (!checklistItem) {
    return false;
  }

  const fileSettings = checklistItem.fileSettings || [];
  const [photoSetting, videoSetting] = parserFilesSetting(fileSettings);

  // Check if there are any file requirements
  const hasPhotoRequirement = photoSetting && photoSetting.minFiles > 0;
  const hasVideoRequirement = videoSetting && videoSetting.minFiles > 0;
  const hasFileRequirement = hasPhotoRequirement || hasVideoRequirement;

  // Get responses (support both response and responses fields)
  const responses = checklistItem.response || checklistItem.responses || [];

  // Check if last response is from current user (need new comment)
  const isNeedComment =
    responses.length === 0 ||
    responses[responses.length - 1]?.isExMyResponse === false;

  // Helper to check if file is photo
  const isPhoto = (file: any): boolean => {
    if (file?.type?.includes('image')) return true;
    if (file?.fileExtension && !VideoType?.includes(file.fileExtension))
      return true;
    return false;
  };

  // Helper to check if file is video
  const isVideo = (file: any): boolean => {
    if (file?.type?.includes('video')) return true;
    if (file?.fileExtension && VideoType?.includes(file.fileExtension))
      return true;
    return false;
  };

  // Collect all files from responses
  const allFileEntities = responses.flatMap(
    (item: any) => item.fileEntities || [],
  );
  const filesFromResponses = allFileEntities.filter(
    (item: any) => !VideoType?.includes(item?.fileExtension),
  );
  const videosFromResponses = allFileEntities.filter((item: any) =>
    VideoType?.includes(item?.fileExtension),
  );

  // Local files being uploaded (from UI state)
  const localFiles = checklistItem.fileEntities || [];
  const localPhotos = localFiles.filter(isPhoto);
  const localVideos = localFiles.filter(isVideo);

  // ========== ROLE-BASED VALIDATION ==========

  // INSPECTOR: Cannot proceed if qiStatus is not Pending
  if (
    checklistItem.role === userRole.INSPECTOR &&
    checklistItem.qiStatus !== qIStatus.Pending
  ) {
    return false;
  }

  // Leader marked as incomplete and isChecked is true  invalid
  if (isNeedComment) {
    if (
      checklistItem.leaderCheckIncomplete &&
      checklistItem.isChecked === true
    ) {
      return false;
    }
  }

  // ========== INSPECTOR SPECIFIC LOGIC ==========
  if (checklistItem.role === userRole.INSPECTOR) {
    if (checklistItem.qiStatus === qIStatus.Pending) {
      if (hasFileRequirement) {
        if (!responses.length) {
          // No responses yet  check local files
          return validateFileRequirements(
            localPhotos.length,
            localVideos.length,
            photoSetting,
            videoSetting,
          );
        } else {
          // Has responses  check total from responses
          return validateFileRequirements(
            filesFromResponses.length,
            videosFromResponses.length,
            photoSetting,
            videoSetting,
          );
        }
      } else {
        // No file requirement but isChecked is false and no responses
        if (!responses.length && checklistItem.isChecked === false) {
          return false;
        }
      }
    }
    return true;
  }

  // ========== NON-INSPECTOR LOGIC ==========
  if (isNeedComment) {
    if (hasFileRequirement) {
      if (!responses.length) {
        // No responses yet  must add files according to fileSettings
        return validateFileRequirements(
          localPhotos.length,
          localVideos.length,
          photoSetting,
          videoSetting,
        );
      } else {
        // Has responses  check total from responses
        return validateFileRequirements(
          filesFromResponses.length,
          videosFromResponses.length,
          photoSetting,
          videoSetting,
        );
      }
    }
  }

  // If no file requirements  pass validation
  if (!hasFileRequirement) {
    return true;
  }

  // Default: Count total files and validate
  const totalPhotos = filesFromResponses.length + localPhotos.length;
  const totalVideos = videosFromResponses.length + localVideos.length;

  return validateFileRequirements(
    totalPhotos,
    totalVideos,
    photoSetting,
    videoSetting,
  );
};

/**
 * Helper function to validate file counts against requirements
 */
const validateFileRequirements = (
  photoCount: number,
  videoCount: number,
  photoSetting: FileSetting | undefined,
  videoSetting: FileSetting | undefined,
): boolean => {
  // Validate photo requirements
  if (photoSetting && photoSetting.minFiles > 0) {
    if (photoSetting.minFiles > photoSetting.maxFiles) {
      return false;
    }
    if (photoCount < photoSetting.minFiles) {
      return false;
    }
    if (photoCount > photoSetting.maxFiles) {
      return false;
    }
  }

  // Validate video requirements
  if (videoSetting && videoSetting.minFiles > 0) {
    if (videoSetting.minFiles > videoSetting.maxFiles) {
      return false;
    }
    if (videoCount < videoSetting.minFiles) {
      return false;
    }
    if (videoCount > videoSetting.maxFiles) {
      return false;
    }
  }

  return true;
};

export const parserFilesSetting = (fileSettings: FileSetting[]) => {
  const photoSetting = fileSettings.find(
    item => item.fileType === FileType.PHOTO,
  );
  const videoSetting = fileSettings.find(
    item => item.fileType === FileType.VIDEO,
  );
  return [photoSetting, videoSetting];
};
