import { useDispatch, useSelector } from 'react-redux';
import { logger } from '../utils';
import { useCallback, useEffect, useRef } from 'react';
import { useFocusEffect } from '@react-navigation/native';
import { StatusBar } from 'react-native';
import { colors } from '../theme';
import { updateProfile } from '../helper';

function hasPendingTrainingWithQuestion(trainingsByCompany: any): boolean {
  if (!Array.isArray(trainingsByCompany)) {
    return false;
  }

  for (const company of trainingsByCompany) {
    const trainings = company?.trainings;
    if (!Array.isArray(trainings)) {
      continue;
    }

    for (const training of trainings) {
      if (!training) {
        continue;
      }

      if (!training.isPassed && !training.isWatched) {
        return true;
      }
    }
  }

  return false;
}

function hasPendingRequiredTutorialVideos(tutorialsByCompany: any): boolean {
  if (!Array.isArray(tutorialsByCompany)) {
    return false;
  }

  for (const company of tutorialsByCompany) {
    const tutorials = company?.tutorials;
    if (!Array.isArray(tutorials)) {
      continue;
    }

    for (const tutorial of tutorials) {
      if (!tutorial) {
        continue;
      }

      if (tutorial.required && !tutorial.isWatched) {
        return true;
      }
    }
  }

  return false;
}

function hasPendingRequiredTutorials(tutorials: any): boolean {
  if (!Array.isArray(tutorials)) {
    return false;
  }

  for (const tutorial of tutorials) {
    if (!tutorial) {
      continue;
    }

    if (tutorial.required && !tutorial.isWatched) {
      return true;
    }
  }

  return false;
}

export const useTraining = () => {
  const dispatch = useDispatch();

  const qualityGuideData = useSelector(
    (state: any) => state.training?.myTrainings?.data,
  );
  const tutorialsData = useSelector(
    (state: any) => state.training?.tutorials?.data?.tutorials,
  );
  const userData = useSelector((state: any) => state.auth.userProfile);

  const isMyTrainingsLoading = useSelector(
    (state: any) => state.training?.myTrainings?.loading,
  );
  const isTutorialsLoading = useSelector(
    (state: any) => state.training?.tutorials?.loading,
  );
  const myTrainingsError = useSelector(
    (state: any) => state.training?.myTrainings?.error,
  );
  const tutorialsError = useSelector(
    (state: any) => state.training?.tutorials?.error,
  );

  // Avoid checking status on the very first mount when data is still empty.
  // We only allow status check after at least one loading cycle occurred.
  const hasSeenLoadingRef = useRef(false);

  const fetchMyTrainings = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      return dispatch(redux.actionContainer.getMyTrainingsAsync());
    } catch (error) {
      logger('error fetchMyTrainings', error);
    }
  }, [dispatch]);

  const fetchAllTutorials = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      return dispatch(redux.actionContainer.getAllTutorialsAsync());
    } catch (error) {
      logger('error fetchAllTutorials', error);
    }
  }, [dispatch]);

  const checkStatusData = useCallback(() => {
    try {
      if (!userData) {
        return;
      }

      const trainingsByCompany = (qualityGuideData as any)?.trainings;
      const tutorialsByCompany = (qualityGuideData as any)?.tutorialsByCompany;

      const hasTrainingNotEligible =
        hasPendingTrainingWithQuestion(trainingsByCompany);
      const hasTutorialVideoNotEligible =
        hasPendingRequiredTutorialVideos(tutorialsByCompany);
      const hasTutorialNotWatchedRequired =
        hasPendingRequiredTutorials(tutorialsData);

      const shouldUpdateProfile =
        !hasTrainingNotEligible &&
        !hasTutorialVideoNotEligible &&
        !hasTutorialNotWatchedRequired;

      if (
        shouldUpdateProfile &&
        (!userData.isFinishedTraining ||
          userData.needWatchVideoNow ||
          userData.isDueTraining)
      ) {
        updateProfile({
          isFinishedTraining: true,
          needWatchVideoNow: false,
          isDueTraining: false,
        });
      }
    } catch (error) {
      logger('error checkStatusData', error);
    }
  }, [qualityGuideData, tutorialsData, userData]);

  useEffect(() => {
    fetchMyTrainings();
    fetchAllTutorials();
  }, [fetchMyTrainings, fetchAllTutorials]);

  useEffect(() => {
    if (isMyTrainingsLoading || isTutorialsLoading) {
      hasSeenLoadingRef.current = true;
    }
  }, [isMyTrainingsLoading, isTutorialsLoading]);

  useEffect(() => {
    if (!userData) {
      return;
    }

    if (!hasSeenLoadingRef.current) {
      return;
    }

    if (isMyTrainingsLoading || isTutorialsLoading) {
      return;
    }

    // If fetching failed, don't auto-update training completion state.
    if (myTrainingsError || tutorialsError) {
      return;
    }

    checkStatusData();
  }, [
    checkStatusData,
    isMyTrainingsLoading,
    isTutorialsLoading,
    myTrainingsError,
    tutorialsError,
    userData,
  ]);

  useFocusEffect(
    useCallback(() => {
      setTimeout(() => {
        StatusBar.setBackgroundColor(colors.mainColorPrimary);
        StatusBar.setBarStyle('light-content');
      }, 300);
    }, []),
  );

  return { loading: Boolean(isMyTrainingsLoading || isTutorialsLoading) };
};
