import {
  BaseContainer,
  HeaderBase,
  Button,
  Text,
  BottomSelector,
} from 'tradewize-component';
import React from 'react';
import { View, ScrollView, TextInput } from 'react-native';
import { navigationRef } from '../../services/NavigationService';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import FastImage from 'react-native-fast-image';
import Video from 'react-native-video';
import { IconClose, IconPlay } from '../../assets/svg';
import type { BottomSelectorOption } from 'tradewize-component';
import { styles } from './styles';
import { useFeedbackAdd } from '../../hooks/useFeedbackAdd';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../theme';

// TODO: Install required packages if not already installed:
// npm install react-native-video
// cd ios && pod install

export default function FeedbackAddScreen() {
  const { t } = useAppTranslation();

  const {
    feedbackType,
    setFeedbackType,
    isFeedbackTypeSelectorVisible,
    setIsFeedbackTypeSelectorVisible,
    pageIssue,
    setPageIssue,
    isPageIssueSelectorVisible,
    setIsPageIssueSelectorVisible,
    description,
    setDescription,
    attachments,
    feedbackTypeOptions,
    pageIssueOptions,
    canSubmit,
    handleSelectFromGallery,
    handleTakePhoto,
    handleRemoveMedia,
    handleSubmit,
  } = useFeedbackAdd();

  return (
    <>
      <BaseContainer>
        <HeaderBase
          title={t('ADD_FEEDBACK')}
          onPressBack={() => {
            navigationRef.goBack();
          }}
          containerStyle={styles.headerContainer}
          titleStyle={styles.headerTitle}
          colorIconLeft={colors.white}
        />

        <ScrollView
          style={styles.scrollView}
          contentContainerStyle={styles.scrollContent}
          keyboardShouldPersistTaps="handled"
        >
          {/* Page Issue Selector */}
          <View style={styles.formGroup}>
            <Text style={styles.label}>
              {t('PAGE_ISSUE')} <Text style={styles.required}>*</Text>
            </Text>
            <Button
              style={styles.selectorButton}
              onPress={() => setIsPageIssueSelectorVisible(true)}
            >
              <Text
                style={[
                  styles.selectorButtonText,
                  pageIssue && styles.selectorButtonTextSelected,
                ]}
              >
                {pageIssue?.label || t('SELECT_PAGE_ISSUE')}
              </Text>
            </Button>
          </View>

          {/* Feedback Type Selector */}
          <View style={styles.formGroup}>
            <Text style={styles.label}>
              {t('FEEDBACK_TYPE')} <Text style={styles.required}>*</Text>
            </Text>
            <Button
              style={styles.selectorButton}
              onPress={() => setIsFeedbackTypeSelectorVisible(true)}
            >
              <Text
                style={[
                  styles.selectorButtonText,
                  feedbackType && styles.selectorButtonTextSelected,
                ]}
              >
                {feedbackType?.label || t('SELECT_FEEDBACK_TYPE')}
              </Text>
            </Button>
          </View>

          {/* Description */}
          <View style={styles.formGroup}>
            <Text style={styles.label}>
              {t('DESCRIPTION')} <Text style={styles.required}>*</Text>
            </Text>
            <TextInput
              style={styles.textAreaInput}
              value={description}
              onChangeText={setDescription}
              placeholder={t('ENTER_DESCRIPTION') || 'Enter description'}
              placeholderTextColor={colors.grayScale70}
              multiline
              numberOfLines={6}
              textAlignVertical="top"
            />
          </View>

          {/* Attachments */}
          <View style={styles.formGroup}>
            <Text style={styles.label}>{t('ATTACHMENTS')}</Text>

            {/* Media Buttons */}
            <View style={styles.mediaButtonsContainer}>
              <Button
                style={styles.mediaButton}
                onPress={handleSelectFromGallery}
                activeOpacity={0.7}
              >
                <Text style={styles.mediaButtonText}>{t('GALLERY')}</Text>
              </Button>

              <Button
                style={styles.mediaButton}
                onPress={handleTakePhoto}
                activeOpacity={0.7}
              >
                <Text style={styles.mediaButtonText}>{t('CAMERA')}</Text>
              </Button>
            </View>

            {/* Media Preview - below buttons */}
            {attachments.length > 0 && (
              <View style={styles.imagesPreviewContainer}>
                {attachments.map((media, index) => (
                  <View key={index} style={styles.imagePreviewItem}>
                    {media?.type === 'video' ? (
                      <View style={styles.videoWrapper}>
                        <Video
                          source={{ uri: media?.uri }}
                          style={styles.imagePreview}
                          resizeMode="cover"
                          paused={true}
                          muted={true}
                        />
                        <View style={styles.videoIndicator}>
                          <SvgXml
                            xml={IconPlay(colors.white)}
                            width={20}
                            height={20}
                          />
                        </View>
                      </View>
                    ) : (
                      <FastImage
                        source={{ uri: media?.uri }}
                        style={styles.imagePreview}
                        resizeMode={FastImage.resizeMode.cover}
                      />
                    )}
                    <Button
                      style={styles.removeImageButton}
                      onPress={() => handleRemoveMedia(index)}
                      activeOpacity={0.7}
                    >
                      <SvgXml
                        xml={IconClose(colors.white)}
                        width={16}
                        height={16}
                      />
                    </Button>
                  </View>
                ))}
              </View>
            )}
          </View>
        </ScrollView>

        {/* Submit Button - Fixed at bottom */}
        <View style={styles.submitButtonContainer}>
          <Button
            style={[
              styles.submitButton,
              !canSubmit && styles.submitButtonDisabled,
            ]}
            onPress={handleSubmit}
            disabled={!canSubmit}
          >
            <Text
              style={[
                styles.submitButtonText,
                !canSubmit && styles.submitButtonTextDisabled,
              ]}
            >
              {t('SUBMIT')}
            </Text>
          </Button>
        </View>
      </BaseContainer>

      {/* Page Issue Selector */}
      <BottomSelector
        title={t('SELECT_PAGE_ISSUE') || 'Select page issue'}
        options={pageIssueOptions}
        isVisible={isPageIssueSelectorVisible}
        onClose={() => setIsPageIssueSelectorVisible(false)}
        onSelect={(option: BottomSelectorOption) => {
          setPageIssue(option);
          setIsPageIssueSelectorVisible(false);
        }}
        containerStyle={styles.selectorContainer}
      />

      {/* Feedback Type Selector */}
      <BottomSelector
        title={t('SELECT_FEEDBACK_TYPE') || 'Select feedback type'}
        options={feedbackTypeOptions}
        isVisible={isFeedbackTypeSelectorVisible}
        onClose={() => setIsFeedbackTypeSelectorVisible(false)}
        onSelect={(option: BottomSelectorOption) => {
          setFeedbackType(option);
          setIsFeedbackTypeSelectorVisible(false);
        }}
        containerStyle={styles.selectorContainer}
      />
    </>
  );
}
